import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.js";
import axios from "axios";
import { useState } from "react";
import "./App.css";
import Header from "./Header.js";




function App() {
  const [data, setData] = useState([]);

  const getDataFromAPI = () => {
    const options = {
      method: "GET",
      url: "https://planets-info-by-newbapi.p.rapidapi.com/api/v1/planets/",
      headers: {
        "X-RapidAPI-Key": "a7860b1798mshd9dc353b8795ec0p100d58jsnf12e94e16813",
        "X-RapidAPI-Host": "planets-info-by-newbapi.p.rapidapi.com",
      },
    };

    axios
      .request(options)
      .then((res) => {
        setData(res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className="App">
      <Header />


      <div className="container-fluid hero">
        <div className="row">
          <div className="col-12 col-md-9 col-lg-6 d-block ms-auto title text-center">
            <h1>GO BEYOND</h1>
            <h1>SPACE</h1>
            <button className="btn btn-success rounded-pill  " onClick={getDataFromAPI}>
       EXPLORE
      </button>
          </div>
        </div>
      </div>
      
      <div className="container-fluid data">
        <div className="container">
          <div className="row justify-content-evenly">
            {data.length > 0 &&
              data.map((info, key) => (
                <div className="col-10 m-2 col-md-5 col-xl-3 border text-white py-2 " key={key}>
                  <div className="card">
                    <img
                      src={info.imgSrc.img}
                      className="card-img-top"
                      alt="image not found"
                      height="270px"
                      
                    />
                  </div>
                  <div className="card-body">
                    <p>{info.planetOrder}</p>
                    <h5 className="card-title">{info.name}</h5>
                    <p className="card-text">{info.description}</p>
                    <a href={info.wikiLink} className="btn btn-success">
                      KNOW MORE
                    </a>
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
